<template>
  <div>
    <div class="card1">
      <meta charset="UTF-8">
      <div class="container">
        <p class="name_city">{{city.name}} - {{city.sys.country}}</p>
        <h3 class="info-title">{{Math.round(city.main.temp)}}° </h3>
        <h3 class="info-title" v-if="this.status != null">{{city.weather[0].description}} {{this.status}}</h3>
        <div class="feels">
          <span>Feels like </span>
          <span><strong>{{Math.round(city.main.feels_like)}}°</strong></span>
        </div>
        <div class="humidity">
          <span>Humidity </span>
          <span><strong>{{city.main.humidity}}%</strong></span>
        </div>
        <div class="temp_max">
          <span class="arrow">↑</span>
          <span><strong>{{Math.round(city.main.temp_max)}}°</strong></span>
          <span class="arrow">↓</span>
          <span><strong>{{Math.round(city.main.temp_min)}}°</strong></span>
        </div>  
      </div>
    </div>
  </div>
</template>

<script>
export default {
    props:["city"],

    data(){
        return{
          status:'',
        } 
    },

    mounted() {
      const temp = this.city.main.temp;
      console.log(this.city.main.temp);
        if(temp >= 25){
            this.status = '🔥';
        }
        if(temp <= 25 && temp >= 18){
            this.status = '⛅';
        }
        if(temp <= 15){
            this.status = '🌧';
        }

    }
}
</script>

