<template>
  <div id="app">

    <section class="home"> 
      <h1 class="title">{{ title }}</h1>
      <div v-if="city != 0">
        <City :city="city"/>
      </div>
      <label>
        <input v-on:keyup.enter="getCity" id='search' type="text" v-model="search" placeholder="Enter the city name here">
      </label>
    </section>
    
    <section class="capitais">
      <hr width="35%">
      <div class="card2">
        <div class="container">
          <h3 class="capitais-titulo">Capitals</h3>
          <p class="capital">Min Máx</p>
          <Capital v-for="(capital, idx) in capital_json" :key="idx" :capital="capital"/>
        </div>
      </div>
    </section>
  
  </div>
</template>

<script>
import axios from 'axios'
import City from './components/City.vue'
import Capital from './components/ Capital.vue'

export default{
  
  data () {
    return{
      title: "Weather forecast",
      api_key: '76660065e3bb6607e13ab040e2a028da',
      url: 'https://api.openweathermap.org/data/2.5/weather',
      capitais: [],
      city:[],
      capital_json:[],
      capital_names:[
        'Rio de Janeiro', 'São Paulo', 'Belo Horizonte', 
        'Brasília', 'Belém', 'Salvador', 'Curitiba',
        'Fortaleza', 'Manaus', 'João Pessoa'
        ],
    }
  },

  components: {
    City,
    Capital,
  },


  mounted(){
    if(this.capital_json == ''){
        this.capital_names.forEach(capitais => {
        axios.get(`${this.url}?q=${capitais}&units=metric&APPID=${this.api_key}`)
            .then(({data}) => (this.capital_json.push(data)));
      });
    }  
  },

  methods:{
    getCity(e){
      const city = e.target.value;

      axios.get(`${this.url}?q=${city}&units=metric&APPID=${this.api_key}`)
           .then(({data}) => (this.city = data));
      
      this.search = '';
      e.target.reset();    
    },

  },

  

}
</script>

